<template>
  <div class="avatar">
    <img src="./../../assets/img/LOGO.png" alt="松立Logo" class="companyLogo"/>
    <h2 class="projectName">慧停车运营平台</h2>
    <img :src="imageUrl" alt="用户头像" class="userAvatar"/>
    <p class="userName">{{trueName}}</p>
    <p class="exit"><a href="javascript:void(0);" @click="logout"><i class="iconfont">&#xe653;</i></a></p>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { getOssByCondition } from '@/api/base'

  export default {
    name: 'op-avatar',
    data () {
      return {
        imageUrl:'http://ozpbi2qso.bkt.clouddn.com/avatar.png'
      }
    },
    created () {
      this.getAvatar(this.avatar)
    },
    computed: {
      ...mapGetters(['trueName', 'avatar'])
    },
    methods: {
      logout () {
        this.$store.dispatch('FedLogOut').then(() => {
          location.reload()// hack路由debug
        })
      },
      // 获取人员头像
      async getAvatar (key) {
        let res = await getOssByCondition(key)
        this.imageUrl = res.returnObject.url
      }
    }
  }
</script>

<style lang="less">
  .avatar {
    height: 320px;
    background: #F2F2F2;
    border-bottom: 1px solid #DFDFDF;
    text-align: center;
    box-sizing: border-box;

    .companyLogo {
      margin-top: 50px;
    }

    .projectName {
      margin-top: 10px;
      font-weight: normal;
    }

    .userAvatar {
      width: 64px;
      height: 64px;
      margin-top: 10px;
      box-shadow: 0 0 0 4px #ddd;
      border-radius: 50%;
    }

    .userName {
      margin-top: 10px;
    }

    .exit {
      margin-top: 10px;
      a {
        text-decoration: none;
      }
    }
  }
</style>



