<template>
  <div class="op-menu-frame">
    <ul>
      <op-menu-item v-for="(menu, index) in menus" :key="index" :menu="menu"></op-menu-item>
    </ul>
  </div>
</template>

<script>
  import opMenuItem from './opMenuItem.vue'

  export default {
    name: 'op-menu',
    props: ['menus'],
    components: {opMenuItem}
  }
</script>

<style lang="less">
.op-menu-frame {
    position: absolute;
    width: 100%;
    top: 320px;
    bottom: 0px;
    overflow: auto;

    .op-menu-item-text {
      height: 40px;
      line-height: 40px;
      padding: 0 20px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      font-size: 14px;
      border-bottom: 1px solid #DFDFDF;
      background: #F2F2F2;

      span {
        cursor: pointer;
      }
    }

    .op-menu-item-text:hover {
      color: #8492a6;
    }

    .op-submenu {
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      background-color: #FFF;

      .op-menu-item-text {
        background-color:#FFFFFF;
      }
    }
  }
</style>



