<template>
  <op-container>
    <op-sidebar :width="sidebarWidth"></op-sidebar>
    <op-body :headHeight="headHeight" :marginLeft="sidebarWidth">
      <!-- <authority-permission></authority-permission> -->
      <router-view> </router-view>
    </op-body>
  </op-container>
</template>

<script>
  import opContainer from '@/components/base/container.vue'
  import opSidebar from '@/components/base/sidebar.vue'
  import opBody from '@/components/base/opBody.vue'
  import authorityPermission from '@/components/authority/permissions/index.vue'

  export default {
    name: 'app',
    data: function () {
      return {
        headHeight: '60px',
        sidebarWidth: '200px'
      }
    },
    components: {opContainer, opBody, opSidebar, authorityPermission}
  }
</script>

<style lang="less"></style>



