<template>
  <li class="op-menu-item">
    <div class="op-menu-item-text" @click="toggle">
        <span>{{ menu.permissionName }}</span>
        <i class="icon iconfont"></i>
    </div>
    <ul class="op-submenu" v-show="open" v-if="isParent">
        <op-menu-item v-for="(item, index) in menu.subList" :key="index" :menu="item"></op-menu-item>
    </ul>
  </li>
</template>

<<script>
export default {
  name: 'op-menu-item',
  props: ['menu'],
  data: function () {
    return {
      open: false
    }
  },
  computed: {
      // 确定是否是父级
    isParent () {
      return this.menu.subList && this.menu.subList.length
    }
  },
  methods: {
    toggle () {
      if (this.menu.subList && this.menu.subList.length) {
        if (this.open === true) {
          this.open = false
        } else {
          this.open = true
        }
      } else {
        window.location.href = this.menu.meanUrl
      }
    }
  }
}
</script>

<<style lang="less">

</style>



