import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  /* 后端支持可开 */
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  base: 'authority/permissions',
  routes: [
    {path: '/permission', component: resolve => require(['components/authority/permissions/permissions.vue'], resolve), meta: {nav: ['平台管理', '权限管理']}},
    {path: '/staff', component: resolve => require(['components/authority/permissions/index.vue'], resolve), meta: {nav: ['平台管理', '运营人员']}},
    {path: '/role', component: resolve => require(['components/authority/permissions/role.vue'], resolve), meta: {nav: ['平台管理', '角色管理']}}
  ]
})



