<template>
  <div id="op-sidebar" class="op-sidebar" :style="{width}">
  <scroll-bar> 
    <div class="sidebar-inner">
      <avatar></avatar>
        <el-menu mode="vertical" :default-active="$route.path" background-color="#F2F2F2" text-color="#333" active-text-color="#ffffff">
          <sidebar-item :routes="permissionList"></sidebar-item>
        </el-menu>
      <!-- <op-menu :menus="permissionList"></op-menu> -->
    </div>
  </scroll-bar>
    
  </div>
  
</template>

<script>
  import avatar from './avatar.vue'
  import opMenu from './opMenu/opMenu.vue'
  import { mapGetters } from 'vuex'
  import SidebarItem from './sideBar/sidebarItem.vue'
  import ScrollBar from 'components/base/scrollBar.vue'
  export default {
    name: 'op-sidebar',
    props: {
      width: {
        type: String,
        default: '200px'
      }
    },
    data () {
      return {
      }
    },
    computed: {
      ...mapGetters(['permissionList'])
    },
    components: {avatar, opMenu, SidebarItem, ScrollBar},
    mounted: function () {
      // {id:'id/编号',name:'名称',target:'目标地址'}
      this.permissions = [{
        id: '1',
        name: '首页',
        target: '/home',
        nav: ['首页']
      }, {
        id: '2',
        name: '平台管理',
        target: 'http://baidu.com',
        menus: [{
          id: '2-1',
          name: '运营人员',
          target: '/authority/permissions',
          nav: ['平台管理', '运营人员']
        }, {
          id: '2-2',
          name: '角色管理',
          target: '/authority/permissions#/role',
          nav: ['平台管理', '角色管理']
        }]
      }]
    }
  }
</script>

<style lang="less">
  .op-sidebar {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    border-right: 1px solid #DFDFDF;
    overflow: hidden;
    .sideber-inner{
      width:100%;
      height: 100%;
      position: relative;
    }
  }
</style>



